import React from 'react';
import { Switch, Route } from 'react-router-dom';
import './App.css';
import Homepage from './pages/Homepage';
import Var2 from './pages/Var2';
import Var3 from './pages/Var3';

function App() {
  return (
    <div className="App">
      <Switch>
          <Route exact
            path="/"
            component={() => <Homepage />}
          />
          <Route exact
            path="/2"
            component={() => <Var2 />}
          />
          <Route exact
            path="/3"
            component={() => <Var3 />}
          />
          {/* <Route 
          path="/404"
          component={() => <NotFound />}
          />
          <Redirect to="/404"/> */}
      </Switch>
    </div>

  );
}

export default App;
